<template>
  <v-list>
    <!--  build avatar user  -->
    <v-list-item class="justify-center pt-4 pb-6">
      <v-avatar color="primary" size="100" v-text="short_hand"> </v-avatar>
    </v-list-item>
    <br />
    <v-list-item>
      <v-list>
        <v-card elevation="5">
          <br />
          <!-- build email  -->
          <v-list-item>
            <v-row align="center">
              <v-col cols="2">
                <v-icon> mdi-email-outline </v-icon>
              </v-col>
              <v-col cols="9">
                <v-list-item-subtitle
                  v-text="user.email"
                ></v-list-item-subtitle>
              </v-col>
            </v-row>
          </v-list-item>
          <!-- build role   -->

          <v-list-item>
            <v-row align="center">
              <v-col cols="2">
                <v-icon>mdi-file-account</v-icon>
              </v-col>
              <v-col cols="9">
                <v-chip :class="getColor(user.role_id)">
                  {{ user.role.name }}
                </v-chip>
              </v-col>
            </v-row>
          </v-list-item>
          <br />
          <!--  this.$vuetify.rtl = true -->
          <v-divider></v-divider>
          <v-list-item @click="changRight">
            <v-list-item-action>
              <v-icon> mdi-repeat </v-icon>
            </v-list-item-action>
            <v-list-item-title>ching Align</v-list-item-title>
          </v-list-item>
          <v-list-item @click="changRight">
            <v-menu top :close-on-click="closeOnClick">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-text="langName"
                  color="primary"
                  outlined
                  v-bind="attrs"
                  v-on="on"
                >
                </v-btn>
              </template>

              <v-list>
                <v-list-item
                  @click="ChangeRtl(item)"
                  v-for="(item, index) in langs"
                  :key="index"
                >
                  <v-list-item-title>{{ item }}</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </v-list-item>
          <v-divider></v-divider>

          <br />

          <v-list-item-title style="padding-left: 10%">
            Mode (dark , Light )</v-list-item-title
          >
          <v-list-item>
            <v-row vertical align="center">
              <v-col cols="3">
                <v-switch v-model="DarkMode"></v-switch>
              </v-col>
              <v-col cols="9" v-if="DarkMode">
                <v-list-item-title> Light</v-list-item-title>
              </v-col>
              <v-col cols="9" v-else>
                <v-list-item-title v-text="'Dark'"></v-list-item-title>
              </v-col>
            </v-row>
          </v-list-item>
          <v-divider></v-divider>
          <center>Colors</center>
          <br />
          <v-container> Dark Mode </v-container>
          <v-list-item>
            <v-row row>
              <v-col cols="6">
                <color-picker
                  :typeColor="'primary'"
                  :typeMode="'dark-primary'"
                  :title="'Change Primary Color in Dark Mode '"
                  :oldColor="this.$vuetify.theme.themes.dark.primary"
                />
              </v-col>
              <v-col cols="6">
                <color-picker
                  :typeColor="'secondary'"
                  :typeMode="'dark-secondary'"
                  :title="'Change Secondary Color in Dark Mode '"
                  :oldColor="this.$vuetify.theme.themes.dark.secondary"
                />
              </v-col>
            </v-row>
          </v-list-item>
          <br />
          <v-container> Light Mode </v-container>
          <v-list-item>
            <v-row row>
              <v-col cols="6">
                <color-picker
                  :typeColor="'primary'"
                  :typeMode="'light-primary'"
                  :title="'Change Primary Color in Dark Mode '"
                  :oldColor="this.$vuetify.theme.themes.light.primary"
                />
              </v-col>
              <v-col cols="6">
                <color-picker
                  :typeColor="'secondary'"
                  :typeMode="'light-secondary'"
                  :title="'Change Secondary Color in Dark Mode '"
                  :oldColor="this.$vuetify.theme.themes.light.secondary"
                />
              </v-col>
            </v-row>
          </v-list-item>
        </v-card>
      </v-list>
    </v-list-item>
  </v-list>
</template>

<script>
import VueCookies from "vue-cookies";

export default {
  data() {
    return {
      // langs: [{ tr: "Turkey" }, { ar: "Syria" }, { en: "Einglish" }],
      langs: ["Turkey", "Arabic", "Einglish"],
      lodLang: "ar",
      closeOnClick: true,
      DarkMode: this.$vuetify.theme.dark,
      right: false,
    };
  },
  watch: {
    DarkMode(state) {
      var setting = VueCookies.get("setting");
      setting.dark = state;
      VueCookies.remove("setting");
      VueCookies.set("setting", setting);
      this.changDarkMode(state);
    },
  },
  computed: {
    langName() {
      return this.$store.getters.lanName;
    },
    user() {
      return this.$store.getters.user;
    },
    short_hand() {
      if (this.user) {
        let words = this.user.name.split(" ");
        if (words[1]) {
          let short_hand = words[0][0] + words[words.length - 1][0];
          return short_hand;
        } else {
          let short_hand = words[0][0];
          return short_hand;
        }
      }
      return "";
    },
  },
  methods: {
    ChangeRtl(lang) {
      VueCookies.remove("lang");
      VueCookies.remove("lang_name");
      VueCookies.set("name_lang", lang);
      if (lang == "Arabic") {
        this.$store.commit("SET_LANG", "ar");
        this.$store.commit("SET_LAN_NAME", lang);
        VueCookies.set("lang", "ar");
        this.$vuetify.rtl = true;
      }
      if (lang == "Turkey") {
        this.$store.commit("SET_LANG", "tr");
        this.$store.commit("SET_LAN_NAME", lang);
        VueCookies.set("lang", "tr");
        this.$vuetify.rtl = false;
      }
      if (lang == "Einglish") {
        this.$store.commit("SET_LANG", "en");
        this.$store.commit("SET_LAN_NAME", lang);
        VueCookies.set("lang", "en");
        this.$vuetify.rtl = false;
      }
    },
    openPickerColor() {
      this.showPickerColor = !this.showPickerColor;
    },
    changDarkMode(state) {
      var setting = VueCookies.get("setting");
      console.log(setting);
      VueCookies.remove("setting");
      setting.dark = state;
      VueCookies.set("setting", setting);
      this.$store.commit("SET_SETTING", setting);
      this.$vuetify.theme.dark = state;
    },
    changRight() {
      this.right = !this.right;
      this.$emit("changRight", this.right);
    },
    getColor(item) {
      // 1 is admin
      // 2 is donations_manager
      // 3 informative
      //  anther is user ...
      if (item == 1) {
        return "primary";
      } else if (item == 2) {
        return "secondary";
      } else if (item == 3) {
        return "red";
      } else {
        return "";
      }
    },
  },
  components: {
    "color-picker": () => import("../components/colorPicker.vue"),
  },
};
</script>

<style>
#email {
  text-size-adjust: auto;
}
#btn {
  width: 100%;
  background-color: rgb(163, 20, 20);
  display: inline;
  font-size: 0.7vw;
}
</style>